.size {
  height: 20px;
  margin: 10px 50px;
}
.container {
  display: flex;
}
.container .fonts {
  display: grid;
  grid-template-columns: repeat(3, minmax(300px, 1fr));
  grid-auto-rows: minmax(240px, auto);
  grid-gap: 40px;
  padding: 50px;
  width: 70%;
  padding-top: 70px;
}
@media (max-width: 1370px) and (min-width: 1000px){
  .container .fonts {
    grid-template-columns: repeat(2, minmax(300px, 1fr));
    grid-gap: 20px;
    width: 70%;
  }
}
@media (max-width: 1000px){
  .container .fonts {
    grid-template-columns: 1fr;
    grid-gap: 20px;
    width: 70%;
  }
}
.container .fonts .font {
  border: 1px solid #dadce0;
  border-radius: 8px;
  cursor: pointer;
}
.container .fonts .font:hover {
  box-shadow: 0 1px 2px 0 rgb(60 64 67 / 30%), 0 2px 6px 2px rgb(60 64 67 / 15%);
}
.container .fonts .font .font__title {
  margin: 16px 16px 24px;
}
.container .fonts .font .font__content {
  padding: 0 16px;
  font-size: 40px;
}
.container .aside {
  position: relative;
  width: 330px;
  height: 100vh;
  top: 0;
  right: 0;
  border-left: 1px solid #dadce0;
  position: fixed;
  background-color: white;
}
.container .aside .aside__basic {
}
.container .aside .aside__basic .basic__title,
.container .aside .aside__notselected .selected__title,
.container .aside .aside__selected .selected__title {
  margin: 16px;
}
.container .aside .aside__basic .basic__font,
.container .aside .aside__notselected .selected__font,
.container .aside .aside__selected .selected__font {
  height: 100px;
  margin: 0 16px;
  padding: 10px;
  border: 1px solid #dadce0;
  border-radius: 8px;
  color: #202124;
}
.container .aside .aside__basic .basic__font,
.container .aside .aside__selected .selected__font {
  font-size: 40px;
}
.container .aside .aside__selected .aside__use {
  margin-top: 30px;
  border-top: 1px solid #dadce0;
  color: #3c4043;
}
.container .aside .aside__selected .aside__use div {
  padding: 16px;
}
.container .aside .aside__selected .aside__use .use__useLink,
.container .aside .aside__selected .aside__use .use__useCss {
  background-color: #f1f3f4;
  font-size: 14px;
}
